angular
  .module('app', ['ui.router', 'ngFlag', 'slick']);

angular
  .module('app')
  .service('Persistence', function () {
    var Persistence = this;
    Persistence.save = function (toBeSaved) {
      localStorage.setItem('mon_espace_de_stockage', JSON.stringify(toBeSaved));
    };

    Persistence.importSaved = function () {
      return JSON.parse(localStorage.getItem('mon_espace_de_stockage'));
    };

    Persistence.supprimerdeListe = function (maListe, el) {
      maListe.splice(maListe.indexOf(el), 1);
      Persistence.save(maListe);
    };

    Persistence.ajouterAListe = function (maListe, el) {
      maListe.push(el);
      Persistence.save(maListe);
    };
  });


angular
  .module('app')
  .component('poster', {
    templateUrl: 'app/component/poster.html',
    controller: function posterController() {
    },
    bindings: {
      movie: '<'
    }
  });


angular
  .module('app')
  .component('movie', {
    templateUrl: 'app/component/movie.html',
    controller: function movieController($stateParams, $http) {
      var $ctrl = this;
      $http.get('https://amc.ig.he-arc.ch/tmdb/movie/' + $stateParams.movieId + '?language=fr')
      .then(function (result) {
        $ctrl.movie = result.data;
      });
    }
  });

angular
  .module('app')
  .component('list', {
    templateUrl: 'app/component/list.html',
    controller: function (Persistence, $http) {
      var $ctrl = this;
      $http.get('https://amc.ig.he-arc.ch/tmdb/movie/popular?language=fr')
      .then(function (result) {
        $ctrl.movies = result.data.results;
      });
      $ctrl.maListe = [];
      $ctrl.ajouterAListe = function (el) {
        Persistence.ajouterAListe($ctrl.maListe, el);
      };
    }
  });

angular
  .module('app')
  .component('favourites', {
    templateUrl: 'app/component/favourites.html',
    controller: function (Save, $http) {
      var $ctrl = this;

      $ctrl.maListe = [];
      $ctrl.maListe = Save.importSaved();
    }
  });

